body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  font-family: "HelveticaNeueCyr-Light";
  font-size: 14px;
}

main {
  flex: 1 0 auto;
}

.text-fl {
  display: inline-block;
  padding-right: 3px;
  letter-spacing: 1.05px;
  font-family: 'HelveticaNeueCyr-Medium';
  color: #000;

  &:first-letter {
    color: #f5003b;
    font-size: 200%;
    line-height: 1.1;
    font-weight: bold;
    font-family: "Georgia";
  }
}

@for $i from 1 through 6 {
  .margin-#{$i*4} {
    margin: #{$i*4}px;
  }

  .margin-bottom-#{$i*4} {
    margin-bottom: #{$i*4}px;
  }
}